  /**----------Reordering of tiles --- */
  public onReorderTileLayout(e: TileLayoutReorderEvent) {
    // prevent the default behavior
    e.preventDefault();
    // check whether the dragged item was dropped over another item
    const targetItem = e.items.filter((item: TileLayoutItemComponent) =>
      this.isOverlappingReorder(e, item)
    )[0];

    e.item.col = e.newCol;
    e.item.row = e.newRow;

    if (targetItem) {
      // swap the dragged and drop target items
      targetItem.col = e.oldCol;
      targetItem.row = e.oldRow;
    }
    console.log(e)
  }

  private isOverlappingReorder(
    e: TileLayoutReorderEvent,
    item: TileLayoutItemComponent
  ): boolean {
    return item.col === e.newCol && item.row === e.newRow;
  }
  /**---------Resizing of Tiles----------- */
  public onResizeTileLayout(e: TileLayoutResizeEvent) {
    e.item.rowSpan = e.newRowSpan;
    e.item.colSpan = e.newColSpan;
    const targetItem = e.items.filter((item) => item !== e.item)[0];

    if (targetItem && this.isOverlappingResize(e.item, targetItem)) {
      targetItem.row = e.item.row + e.item.rowSpan;
    }
  }

  private isOverlappingResize(
    resizedItem: TileLayoutItemComponent,
    otherItem: TileLayoutItemComponent
  ): boolean {
    const hasCommonColumns =
      resizedItem.col + resizedItem.colSpan - 1 >= otherItem.col;
    const hasCommonRows =
      resizedItem.row + resizedItem.rowSpan - 1 >= otherItem.row;
    return hasCommonColumns && hasCommonRows;
  }
