import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  // public bmlfonGetDashboardData(headerData : any): Observable<ODataModel> {
  //   const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetLookupData`;
  //   return this.httpExt.post<ODataModel>(url, headerData, null);
  // }

  public bmlfGetDashboardPageData() : Observable<any>{
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetDashboardPageData`;
    return this.httpExt.get<any>(url,null);
  }
  public bmlfGetDashboardData(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetDashboardData`;
    return this.httpExt.post<any>(url, headerData, null);
  }
  public bmlfGetDashboardLayoutData(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetDashboardLayoutData`;
    return this.httpExt.post<any>(url, headerData, null);
  }
  public bmlfSaveDashboardLayoutData(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveDashboardLayoutData`;
    return this.httpExt.post<any>(url, headerData, null);
  }
  public bmlfSaveDefaultDashboard(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveUserWiseDefaultDashboard`;
    return this.httpExt.post<any>(url, headerData, null);
  }
  public bmlfGetGridViewData(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetDBGridViewData`;
    return this.httpExt.post<any>(url, headerData, null);
  }
}
