import { Component, ViewChild, ViewChildren, QueryList, Console } from '@angular/core';
import { ElementRef } from '@angular/core';
import { HostListener } from '@angular/core';
import { ExcelExportData } from '@progress/kendo-angular-excel-export';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { GridComponent, DataStateChangeEvent } from '@progress/kendo-angular-grid';
import { filterBy, GroupDescriptor, process } from '@progress/kendo-data-query';
import { exportPDF, Group } from '@progress/kendo-drawing';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { UserFieldService } from '../http/userfield.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import * as moment from 'moment';
@Component({
    template: ''
})
export abstract class dashboardbasegrid {
    @ViewChild('exportPDFBtn') exportPDFBtn: ElementRef;
    @ViewChild('exportExcelBtn') exportExcelBtn: ElementRef;
    //pdf print
    @ViewChildren(GridComponent)
    public grids: QueryList<GridComponent>;
    // pdf data
    public groups: GroupDescriptor[] = [];
    public sortable = { allowUnsort: false };
    public applicationName: string = '';
    public applicationLogo = 'assets/images/eworkplacelogo.png';

    public loggedInUser: string = '';
    public currentTimeZone: string = '';
    public currentDateTimeFormat: string = '';

    // for page size option
    public buttonCount = 10;
    public info = true;
    public type: 'numeric' | 'input' = 'numeric';
    public pageSizes = true;
    public previousNext = true;
    //public pageSize = 100;
    public pageSize = 10; //18/Dec/2019 Deepak Porwal (As per discussion for the fast loading of lookups set page size to 10 )

    // Added for loader
    public loadingEnabled: boolean = true;

    // Calculate width for responsive
    public windowWidth: number;

    // Enable/Disable Grid
    public groupingEnabled: boolean = true;
    public filterEnabled: boolean = false;
    public columnMenuEnabled: boolean = true;

    // Date Filter
    public openedDatePicked: boolean = false;
    public selectedDateLbl: string = ''; //  Date Filter label
    public isSelectedDateLbl: number = 0;
    public currentDateTime: string;
    // show active/inactive in grid
    public activeFilterList = [
        {
            'Text': 'Active',
            'Status': 'Active'
        },
        {
            'Text': 'Inactive',
            'Status': 'Inactive'
        },
        {
            'Text': 'Deleted',
            'Status': 'Deleted'
        }
    ];
    public currentState;
    public filterTrue;
    public exportData;
    abstract gridDataList: any;
    public filterCollapsed = false;
    public keyword = '';
    public pageable: any;
    public mobileViewEnabled = false;
    public desktopMedia = '(min-width: 768px)';
    public mobileMedia = '(max-width: 767px)';
    public mobjGridSettings: GridSettings = {
        state: {
            skip: 0,
            take: 20,
            group: [],
            filter: {
                logic: 'and',
                filters: []
            }
        },
        columnsConfig: [],
        gridData: { "data": [], "total": 0 },
        filterEnabled: false
    };
    constructor(
        public localStorageService: LocalStorageService,
        public dateFormatService: DateFormatService,
        public userFieldService: UserFieldService,
        public toastMessageService: ToastMessageService,
        public utilService: AppUtility
    ) {
        // save settings
        this.pageable = {
            buttonCount: 10,
            info: true,
            type: this.type,
            pageSizes: true,
            previousNext: true
        }
        // save settings

    }

    // Grid settings on mobile view
    @HostListener('window:resize', ['$event'])
    onResize(event) {
        this.getWindowWidth();
    }

    public initGrid() {
        this.allData = this.allData.bind(this);
        this.setPdfData();
    }
    public onFilterShow() {
        this.filterCollapsed = !this.filterCollapsed;
    }

    public setExportData(data) {
        this.exportData = {
            "pageName": data.pageName,
            "allData": this.allData
        }
    }

    // for responsive
    public getWindowWidth() {
        this.windowWidth = window.innerWidth;
        if (this.windowWidth < 768) {
            this.groupingEnabled = false;
            this.filterEnabled = false;
            this.columnMenuEnabled = false;
            // Save Settings
            this.mobileViewEnabled = true;
        } else {
            this.groupingEnabled = true;
            // this.filterEnabled = false;
            this.columnMenuEnabled = true;
            // Save Settings
            this.mobileViewEnabled = false;
        }
    }

    // added new for trigger external click of export excel and PDF
   
    // public exportToPDF(grid: GridComponent): void {
    //     grid.saveAsPDF();
    // }

    public exportPDFClick(): void {
        const exportPDF: HTMLElement = this.exportPDFBtn.nativeElement as HTMLElement;
        exportPDF.click();
    }

    public exportExcelClick(): void {
        const exportExcel: HTMLElement = this.exportExcelBtn.nativeElement as HTMLElement;
        exportExcel.click();
    }

    //added for print
    public printToPDF(grid) {
        const promises = this.grids.map(grid => grid.drawPDF());
        Promise.all(promises).then(groups => {
            const rootGroup = new Group({
                pdf: {
                    multiPage: true
                }
            });
            groups.forEach((group) => {
                rootGroup.append(...group.children);
            });
            let option = {
                paperSize: "A3",
                margin: { bottom: "70px", top: "30px", left: "30px", right: "30px" },
                scale: 0.8,
                landscape: false,
                title: this.exportData.pageName,
                template: (data) => `<div style="position:absolute; padding-top:10px; width:100%; border-top:1px solid #e5e5e5; left:0; text-align:center; bottom:10px; font-size:12px; z-index:2;">sd</div>`
            }
            //   let option = {
            //     paperSize: "A4",
            //     margin: {bottom:"70px",top:"30px",left:"30px",right:"30px"},
            //     scale: 0.8,
            //     landscape:true,
            //     title:this.exportData.pageName,
            //     template: (data) => `<div style="position:absolute; padding-top:10px; width:100%; border-top:1px solid #e5e5e5; left:0; text-align:center; bottom:10px; font-size:12px; z-index:2;">sd</div>`
            //   }
            return exportPDF(rootGroup, option);
        }).then(dataUri => {
            let checkIE = this.msieversion();
            if (checkIE) {
                grid.saveAsPDF();
            }
            else {
                let ifrm = document.createElement("embed");
                ifrm.setAttribute("src", dataUri);
                ifrm.setAttribute("width", "100%");
                ifrm.setAttribute("height", "100%");
                let pageStyle = '<style type="text/css">*{margin:0; padding:0;}</style>';
                let newHTML = '<html><head><title>' + this.exportData.pageName + '</title>' + pageStyle + '</head><body>' + ifrm.outerHTML + '</body>';
                let newWindow = window.open("");
                newWindow.document.write(newHTML, '');
            }
        });
    }

    public setPdfData() {
        this.loggedInUser = this.localStorageService.getUserSession().UserName;
        this.applicationLogo = 'assets/images/logo.png';
        // let msCustomDateFormat = this.dateFormatService.getDateFormat();
        // this.currentDateTime = moment().format(msCustomDateFormat.toUpperCase() + ' h:mm:ss a');
        // if (this.localStorageService.getTopHeaderLeftLogoUrl() != null) {
        //     if (this.localStorageService.getTopHeaderLeftLogoUrl().trim().length > 0) {
        //         this.applicationLogo = this.localStorageService.getTopHeaderLeftLogoUrl().trim()
        //     }
        //     this.loggedInUser = this.localStorageService.getUserName();
        // }
    }

    // get all the data from grid.
    public allData(gridName): ExcelExportData {
        const result: ExcelExportData = {
            data: this.getExportDataList(gridName)
        };
        return result;
    }

    public dataStateChange(state: DataStateChangeEvent): void {
        this.currentState = state;
        this.filterTrue = true;
        this.allData = this.allData.bind(this);
        // save settings
        this.mobjGridSettings.state = state;
        this.groups = state.group;
        this.mobjGridSettings.state.group = this.groups
        this.mobjGridSettings.gridData = process(this.gridDataList, this.mobjGridSettings.state
        )
        // save settings
    }

    public getExportDataList(gridName): any {
        if (gridName.filter.filters.length > 0) {
            return filterBy(this.gridDataList, gridName.filter.filters);
        } else {
            return this.gridDataList;
        }
    }
    private msieversion(): boolean {
        // check for IE 11 and Edge
        if (/rv:11.0/i.test(navigator.userAgent) || /Edge\/\d./i.test(navigator.userAgent)) {
            return true;
        }
        else {
            return false
        }
    }
    abstract getDataList(): void;
    // Map saved grid view
    private mapGridSettings(gridSettings: GridSettings): GridSettings {
        const state = gridSettings.state;
        this.mapDateFilter(state.filter);
        return {
            state,
            columnsConfig: gridSettings.columnsConfig.sort((a, b) => a.orderIndex - b.orderIndex),
            gridData: process(this.gridDataList, state),
            name: gridSettings.name,
            filterEnabled: gridSettings.filterEnabled,
        };
    }

    // Apply Date filter
    private mapDateFilter = (descriptor: any) => {
        const filters = descriptor.filters || [];
        filters.forEach(filter => {
            if (filter.filters) {
                this.mapDateFilter(filter);
            }
        });
    }
    bmlfOnVisibilityChange(e: any): void {
        e.columns.forEach(column => {
            this.mobjGridSettings.columnsConfig.find(col => col.field === column.field).hidden = column.hidden;
        });
    }
    bmlfOnReorder(e: any): void {
        this.bmlfOnArrayMove(this.mobjGridSettings.columnsConfig, e.oldIndex, e.newIndex)
    }

    bmlfOnArrayMove(arr, fromIndex, toIndex) {
        var element = arr[fromIndex];
        arr.splice(fromIndex, 1);
        arr.splice(toIndex, 0, element);
    }
    bmlfOnResize(e: any): void {
        e.forEach(item => {
            this.mobjGridSettings.columnsConfig.find(col => col.field === item.column.field).width = item.newWidth;
        });
    }
    // Set Inital Grid View Settings
    public setInitGridSettings(parrSettingData: string, localizeName: string) {
        this.mobjGridSettings = new GridSettings();
        let psGridDateFormat = '{0:' + this.dateFormatService.getDateFormat() + '}'
        if (parrSettingData != "") {
            let parrGridSetting = this.mapGridSettings(JSON.parse(parrSettingData));
            let parrColumnset = parrGridSetting.columnsConfig;
            parrColumnset.forEach(ele => {
                if (ele.type == "date") {
                    ele.format = psGridDateFormat;
                } else {
                    ele.format = "";
                }
            })
            parrGridSetting.columnsConfig = parrColumnset;
            this.mobjGridSettings = parrGridSetting;
        }
        else {
            const parrColumnList = Object.keys(this.gridDataList[0]);
            parrColumnList.forEach(element => {
                this.mobjGridSettings.columnsConfig.push({
                    field: element, title: localizeName == "" ? element.toUpperCase() : localizeName + "." + element.toUpperCase(), filter: '', type: 'text', filterable: true, format: "",
                    hidden: false, width: 100
                });
            });
            this.mobjGridSettings.state.group = [];
            this.mobjGridSettings.state.take = 20;
            this.mobjGridSettings.state.skip = 0;
            this.mobjGridSettings.state.filter = {
                logic: 'and',
                filters: []
            };
            this.mobjGridSettings.gridData = process(this.gridDataList, {
                // Initial filter descriptor
                skip: 0,
                take: 20,
                group: [],
                filter: {
                    logic: 'and',
                    filters: []
                }
            })
            // this.gridSettings.name = 'Default View';
            //this.radioSelected = 'Default View';
            //  this.gridSettings.columnsConfig = this.getGridColums();
        }
        this.filterEnabled = this.mobjGridSettings.filterEnabled;
        this.getWindowWidth();
    }

    bmlfOnSaveGrid(screenName: string): void {
        delete this.mobjGridSettings.gridData;
        this.mobjGridSettings.filterEnabled = this.filterEnabled;
        this.mobjGridSettings.state.skip = 0; //to save always page no. 0 21/04/2020 - RD
        let reqData: any = {
            JSON: JSON.stringify({
                HeaderData: [{ "ScreenName": screenName, "JsonString": JSON.stringify(this.mobjGridSettings) }],
                Response: [{
                    "ResponseText": "", "ErrorCode": ""
                }]
            })
        };
        this.userFieldService.bmlfSaveGridSetting(reqData).subscribe(res => {
            if (res == null) {
                this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("COMMON.MSG.SERVERERROR"));
                return false;
            }
            if (res.Response[0].ErrorCode == "") {
                {
                    this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVEGRIDSETTING"));
                }
            } else {
                this.toastMessageService.notifyError(res.Response[0].ResponseText)
            }
        })
    }
    bmlfOnResetGridSettings(screenName: string, localizeName: string) {
        let reqData: any = {
            JSON: JSON.stringify({
                HeaderData: [{ "ScreenName": screenName, "JsonString": JSON.stringify(this.mobjGridSettings) }],
                Response: [{
                    "ResponseText": "", "ErrorCode": ""
                }]
            })
        };
        this.userFieldService.bmlfResetGridSetting(reqData).subscribe(res => {
            if (res == null) {
                this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("COMMON.MSG.SERVERERROR"));
                return false;
            }
            if (res.Response[0].ErrorCode == "") {
                {
                    this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.RESETGRIDSETTING"));
                    this.setInitGridSettings(res.DefaultGridSettings[0].JSONString, localizeName);
                }
            } else {
                this.toastMessageService.notifyError(res.Response[0].ResponseText)
            }
        })
    }
}
